.text-input {
    border: 1px solid #cacccd;
    height: 48px;
    font-size: $font-size-large;
    font-weight: 300;
    padding: $s-size;
}

.select {
    @extend .text-input;//inherit the properties from the specify class
}

.text-area {
    @extend .text-input;
    height: 10rem;
}